___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  box-sizing: border-box;
  list-style-type: none;
  button {
    text-align: center;
    justify-items: end;
    background-color: #add8e6;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s; /* 添加过渡效果 */
  }
}
header {
  background-color: #7887ab;
  text-align: center;
  color: #2f4372;
  padding: 3rem;
}
body {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  background: linear-gradient(100deg, #2f4372, #4f628e, #7887ab);
  .pageContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 90vh;
    color: #fff;
    text-align: center;
    gap: 50px;
    #app {
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: 90vh;
      color: #fff;
      text-align: center;
      gap: 10px;
      ul {
        list-style-type: none;
        padding: 10px;
        margin: 0;
        max-height: 80vh;
        /* place-items: center; */

        li {
          display: grid;
          grid-template-columns: 10fr repeat(2, 1fr);
          grid-template-rows: 1fr;
          grid-column-gap: 0px;
          grid-row-gap: 0px;
          gap: 15px;
          height: 40px;
          place-items: center;
          font-size: 16px;
          span {
            cursor: pointer;
          }
          button {
            text-align: center;
            justify-items: end;
            background-color: #add8e6;
            color: white;
            border: none;
            border-radius: 5px;
            padding: 10px 20px;
            font-size: 16px;
            cursor: pointer;
            transition: background-color 0.3s, transform 0.2s; /* 添加过渡效果 */
          }
          &:hover {
            transform: translateY(-2px); /* 点击后下降2px */
            background-color: #90a4c7;
          }
          margin-bottom: 10px;
          input[type="checkbox"] {
            margin-right: 5px;
          }
        }
      }
    }
  }
}

p {
  white-space: nowrap;
}

.modal-content {
  background-color: #f3f3ee;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  min-width: 800px;
  gap: 10px;
  color: #000;
  #add-task-section {
    #due-date {
      background-color: #add8e6;
    }
    #create-task {
      background-color: #add8e6;
    }
  }
  #view-task-section {
    #task-list {
      li {
        display: grid;
        grid-template-columns: 8fr 2fr 2fr;
        grid-template-rows: 1fr;
        grid-column-gap: 10px;
        grid-row-gap: 10px;
        margin-top: 15px;
        p {
          cursor: pointer;
          min-width: 80px;
          height: 30px;
          font-size: 16px;
          display: flex;
          justify-content: center;

          &:hover {
            transform: translateY(-2px); /* 点击后下降2px */
            background-color: #add8e6;
            color: white;
          }
        }
        button {
          min-width: 80px;
          max-height: 30px;
        }
      }
    }
  }
  #view-task-detail {
    h4 {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: 1fr;
      margin-bottom: 40px;
      background-color: #f3f3ee;
    }
    p {
      display: grid;
      grid-template-columns: 1fr 6fr 1fr;
      grid-template-rows: 1fr;
    }
  }
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}
.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
.priorityBox {
  display: flex;
  gap: 10px;
  text-align: center;
  justify-content: center;
}

/* 

:root {
  --clr-primary: rgb(54, 112, 199);
  --clr-light: #f4f4f4;
  --clr-dark: #333;
  --clr-warning: rgb(99, 36, 36);
}

*/

*::before,
*::after {
  font-family: inherit;
  box-sizing: border-box;
}

form {
  display: flex;
}

.btn {
  cursor: pointer;
  background: 0;
  border: 0;
  padding: 0;
  color: inherit;
}

.btn.create {
  font-size: 1.5rem;
  font-weight: 900;
  margin-right: 0.25em;
  transition: opacity 250ms ease-in;
}

.btn.create:hover {
  opacity: 0.7;
}

.todo-header {
  padding: var(--spacer);
  background: #e4e4e4;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

[type="checkbox"] {
  opacity: 0;
  position: absolute;
}

.task label {
  display: flex-inline;
  align-items: center;
  position: relative;
}

.task {
  position: relative;
  margin-bottom: 1.25em;
}

.task::after {
  content: "";
  position: absolute;
  right: 0;
  left: 0;
  bottom: -0.5em;
  height: 1px;
  background: currentColor;
  opacity: 0.1;
}

.priority-high {
  background-color: #ff6961;
}
.priority-medium {
  background-color: #ffc300;
}
.priority-low {
  background-color: #77dd77;
}
.priority-none {
  background-color: #d3d3d3;
}
footer {
  text-align: center;
  padding: 15px;
  background-color: #7887ab;
  font-size: 14px;
}
